import React from "react";
import "../styles.css";
export const Component = ({
  count,
  handleIncrementClick,
  handleDecrementClick
}) => (
  <div>
    <h1>
      React-Readux Counter!
      <br /> Current Value: <span>{count}</span>
    </h1>
    <button className="inc" onClick={handleDecrementClick}>
      Decrement
    </button>
    <button className="dec" onClick={handleIncrementClick}>
      Increment
    </button>
  </div>
);
